import React, { useContext, useEffect, useState } from 'react';
import {
    TextField,
    Checkbox,
    Button,
    Typography,
    Link,
    Grid,
    Avatar,
    Box,
    Container,
    FormControlLabel
} from "@mui/material";
import { Adb } from "@mui/icons-material";
import axios from "axios";
import { useDispatch, useSelector } from 'react-redux';
import { setAccessToken, setHours, setMinutes, setNickname, setSeconds, setTimer } from '../redux/actions';
import axiosInstance from './setting/axiosInstance';

function Login() {
    const accessToken = useSelector((state) => state.accessToken);
    const nickname = useSelector((state) => state.nickname);

    axios.defaults.withCredentials = true;

    const dispatch = useDispatch();

    const [inputId, setInputId] = useState("");
    const [inputPw, setInputPw] = useState("");


    const handleInputId = (e) => {
        setInputId(e.target.value);
        console.log(inputId)
    };

    const handleInputPw = (e) => {
        setInputPw(e.target.value);
    };

    const handleClick = () => {
        console.log("click login");
        console.log("ID : ", inputId);
        console.log("PW : ", inputPw);

        //    .
        const config = {
            withCredentials: true, //  HttpOnly   .
            headers: {
                "Content-Type": "application/x-www-form-urlencoded",
            },
        };

        axios.post('/register/login', {
            email: inputId,
            password: inputPw,
        }
            , config)
            .then((res) => {
                console.log(res.data.nickname);
                dispatch(setNickname(res.data.nickname));
                dispatch(setAccessToken(res.data.accessToken));
                dispatch(setHours(10));
                dispatch(setMinutes(0));
                dispatch(setSeconds(0));

                //      0.5  
                setTimeout(() => {
                    window.location.replace('/');
                }, 500);
            })
            .catch(error => {
                alert(error);
            });
    };

    return (
        <Container component="main" maxWidth="xs">
            <Box sx={{ marginTop: 8, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <Adb />
                </Avatar>
                <Typography component='h1' variant='h5'>
                    
                </Typography>
                <TextField label="" required fullWidth style={{ margin: 10 }} name='id' onChange={handleInputId} />
                <TextField label="" type='password' required fullWidth name='password' onChange={handleInputPw} />
                <FormControlLabel control={<Checkbox value='reUser' color='primary' />} label=' ' />
                <Button type='submit' variant='contained' sx={{ marginTop: 2 }} onClick={handleClick}></Button>
                <Grid container>
                    <Grid item xs>
                        <Link href='/findPassword'>
                             
                        </Link>
                    </Grid>
                    <Grid item>
                        <Link href='/join'>
                            
                        </Link>
                    </Grid>
                </Grid>
            </Box>
        </Container>
    );
}

export default Login;