import React from 'react';
import { Button } from 'react-bootstrap';
import { useDispatch, useSelector } from "react-redux";
import { setAccessToken, setRefreshToken } from '../redux/actions';

function Myjo() {
    const StorerefreshToken = useSelector((state) => state.refreshToken);
    const StoreaccessToken = useSelector((state) => state.accessToken);

    const dispatch = useDispatch();

    const handlecli = () => {
        dispatch(setAccessToken("asdf"));
    };

    const handlecli123 = () => {
        console.log(StoreaccessToken);

    };

    return (
        <div>
            <Button onClick={handlecli}></Button>
            <Button onClick={handlecli123}></Button>
        </div>
    );
}


export default Myjo;