// src/main/frontend/src/App.js
import "./App.css"
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Header from "./component/Header";
import Mix from "./component/mix";
import Login from "./component/Login";
import Myjo from "./component/Myjo";
import Notify from "./component/notify/Notify";
import Join from "./component/join";
import List from "./component/board/list";
import Write from "./component/board/write";
import React, { useMemo, useState } from "react";
import Logout from "./component/logout";
import FindPassword from "./component/findPassword";
import NRead from "./component/notify/Nread";
import BRead from "./component/board/Bread";
import { Provider } from "react-redux";
import { PersistGate } from 'redux-persist/integration/react';
import { persistor, store } from "./redux/store";
import Main from "./component/main";




function App() {


    return (
        <Provider store={store} >
            <PersistGate loading={null} persistor={persistor}>
                <BrowserRouter>
                    <Header />
                    <Routes>
                        <Route path="/" element={<Main />}></Route>
                        <Route path="/mix" element={<Mix />}></Route>
                        <Route path="/myjo" element={<Myjo />}></Route>
                        <Route path="/notify" element={<Notify />}></Route>
                        <Route path="/notify/read/:nno" element={<NRead />}></Route>
                        <Route path="/board/List" element={<List />}></Route>
                        <Route path="/board/write" element={<Write />}></Route>
                        <Route path="/board/read/:bno" element={<BRead />}></Route>
                        <Route path="/login" element={<Login />}></Route>
                        <Route path="/join" element={<Join />}></Route>
                        <Route path="/logout" element={<Logout />}></Route>
                        <Route path="findPassword" element={<FindPassword />}></Route>
                    </Routes>
                </BrowserRouter>
            </PersistGate>
        </Provider>

    );
}


export default App;

