import React, { useEffect, useState, useTransition } from 'react';
import { Box, TextField, Button, MenuItem } from "@mui/material";
import { useSelector } from 'react-redux';
import axiosInstance from './setting/axiosInstance';
import { Menu, useAutocomplete } from '@mui/base';

function Mix() {
    const StoreaccessToken = useSelector((state) => state.accessToken);
    const [dropdownList, setDropdownList] = useState([])
    // 
    const [isButtonDisabled, setIsButtonDisabled] = useState(false);


    //
    const [inputValue, setInputValue] = useState('');
    const [suggestions, setSuggestions] = useState([]);


    const [message, setMessage] = useState('');
    const [corelist, setCoreList] = useState([{
        core_name: "",
        min_lv: "",
        max_lv: "",
        main_core: "",
        sub_core: "",
    }]);


    useEffect(() => {
        axiosInstance.defaults.headers.common['Authorization'] = `${StoreaccessToken}`;
        axiosInstance.get('/core/list')
            .then(response => {
                setDropdownList(response.data)
            })
            .catch(error =>
                console.log)
    }, [])

    // 3 
    const ButtonDisable = () => {
        setIsButtonDisabled(true); //  
        setTimeout(() => {
            setIsButtonDisabled(false); //  (3)   
        }, 3000);
    };


    //       
    const handleInputChange = (e) => {
        const input = e.target.value;
        setInputValue(input);

        //     
        const filteredSuggestions = dropdownList.filter((keyword) =>
            keyword.includes(input)

        )
            .slice(0, 3);;

        setSuggestions(filteredSuggestions);
    };

    const handleChange = (e) => {
        setMessage(e.target.value);
    };

    const clickItem = (e) => {
        setMessage(e);
        setInputValue(e)
    };


    //
    const handleClick = () => {
        axiosInstance.defaults.headers.common['Authorization'] = `${StoreaccessToken}`;
        axiosInstance.get('/core/search/' + message)
            .then(response =>
                setCoreList(response.data),
                ButtonDisable()
            )
            .catch(error => {
                console.error(error);
                ButtonDisable();
            })
    };

    const printList = () => {
        return (
            <div>
                <div>
                    <span className="onelayer" style={{ display: "block" }}>
                        <table align="center" style={{ width: "300px", backgroundColor: "#778899", color: "black", textAlign: "center", marginLeft: "500px" }}>
                            <tbody>
                                <th colSpan="2">{corelist[0].core_name} </th>
                                <tr>
                                    <td>LV : {corelist[0].min_lv}~{corelist[0].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>
                </div>

                <div>
                    <span style={{ float: "left", display: "inline-block" }}>
                        <table style={{
                            width: "200px",
                            marginLeft: "350px",
                            marginTop: "50px",
                            backgroundColor: "#778899",
                            color: "black",
                            textAlign: "center"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[0].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[1].min_lv}~{corelist[1].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "right", display: "inline-block" }}>
                        <table style={{
                            width: "200px",
                            marginRight: "200px",
                            marginTop: "50px",
                            backgroundColor: "#778899",
                            color: "black",
                            textAlign: "center"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[0].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[2].min_lv}~{corelist[2].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>
                </div>

                <div>
                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "200px",
                            marginTop: "50px",
                            backgroundColor: "#99e699",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[1].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[3].min_lv}~{corelist[3].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>

                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "180px",
                            marginTop: "50px",
                            backgroundColor: "#4dffb8",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[1].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[4].min_lv}~{corelist[4].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "170px",
                            marginTop: "50px",
                            backgroundColor: "#9999ff",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[2].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[5].min_lv}~{corelist[5].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>

                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "180px",
                            marginTop: "50px",
                            backgroundColor: "#7070db",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[2].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[6].min_lv}~{corelist[6].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>
                </div>

                <div>
                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "120px",
                            marginTop: "50px",
                            backgroundColor: "#99e699",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[3].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[7].min_lv}~{corelist[7].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>

                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#99e699",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[3].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[8].min_lv}~{corelist[8].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#4dffb8",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[4].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[9].min_lv}~{corelist[9].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>

                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#4dffb8",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[4].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[10].min_lv}~{corelist[10].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#9999ff",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[5].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[11].min_lv}~{corelist[11].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#9999ff",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[5].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[12].min_lv}~{corelist[12].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#7070db",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[6].main_core} </th>
                                <tr>
                                    <td>LV : {corelist[13].min_lv}~{corelist[13].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>


                    <span style={{ float: "left", display: "block" }}>
                        <table style={{
                            width: "100px",
                            marginLeft: "40px",
                            marginTop: "50px",
                            backgroundColor: "#7070db",
                            color: "black"
                        }}>
                            <tbody>
                                <th colSpan="2">{corelist[6].sub_core} </th>
                                <tr>
                                    <td>LV : {corelist[14].min_lv}~{corelist[14].max_lv}</td>
                                </tr>
                            </tbody>
                        </table>
                    </span>
                </div>
            </div>
        );
    }

    return (
        <div style={{ marginTop: "5px" }}>
            <Box>
                <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2 }} disabled={isButtonDisabled} onClick={handleClick}>  </Button>
                <TextField
                    id='coreserchField'
                    required
                    sx={{ width: 200 }}
                    placeholder="Search..."
                    value={inputValue}
                    onChange={handleInputChange}
                    onInput={handleChange}
                />
                <Menu>
                    {suggestions.map((keyword, index) => (
                        <MenuItem sx={{ width: 300 }} key={index} onClick={() => { clickItem(keyword) }}>{keyword}</MenuItem>
                    ))}
                </Menu>

            </Box>





            <Box sx={{ marginTop: 8, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <div>
                    {
                        corelist[1] && <div>{printList(corelist)}</div>
                    }
                </div>
            </Box>

        </div>
    );
}


export default Mix;