import { combineReducers } from "redux";
import { SET_ACCESS_TOKEN, SET_Hours, SET_Minutes, SET_NICKNAME, SET_REFRESH_TOKEN, SET_Seconds } from "./actions";

const accessTokenReducer = (state = "", action) => {
    switch (action.type) {
        case SET_ACCESS_TOKEN:
            return action.payload;
        default:
            return state; //    
    }
};

const refreshTokenReducer = (state = "", action) => {
    switch (action.type) {
        case SET_REFRESH_TOKEN:
            return action.payload;
        default:
            return state; //    
    }
};

const nicknameReducer = (state = "", action) => {
    switch (action.type) {
        case SET_NICKNAME:
            return action.payload;
        default:
            return state; //    
    }
};
const hoursReducer = (state = 0, action) => {
    switch (action.type) {
        case SET_Hours:
            return action.payload;
        default:
            return state; //    
    }
};
const minutesReducer = (state = 0, action) => {
    switch (action.type) {
        case SET_Minutes:
            return action.payload;
        default:
            return state; //    
    }
};
const secondsReducer = (state = 0, action) => {
    switch (action.type) {
        case SET_Seconds:
            return action.payload;
        default:
            return state; //    
    }
};

const rootReducer = combineReducers({
    accessToken: accessTokenReducer,
    refreshToken: refreshTokenReducer,
    nickname: nicknameReducer,
    hours: hoursReducer,
    minutes: minutesReducer,
    seconds: secondsReducer
});

export default rootReducer;





