import { colors } from '@mui/material';
import React, { useContext, useEffect, useState } from 'react'
import { NavItem } from 'react-bootstrap';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useDispatch, useSelector } from 'react-redux';
import { setHours, setMinutes, setSeconds } from '../redux/actions';

export default function Header() {
    const StoreAccessToken = useSelector((state) => state.accessToken);
    const StoreNickname = useSelector((state) => state.nickname);
    const StoreHours = useSelector((state) => state.hours);
    const StoreMinuets = useSelector((state) => state.minutes);
    const StoreSeconds = useSelector((state) => state.seconds);

    const dispatch = useDispatch();

    useEffect(() => {
        if (StoreAccessToken != "") {
            const countdown = setInterval(() => {
                if (parseInt(StoreSeconds) > 0) {
                    dispatch(setSeconds(parseInt(StoreSeconds) - 1));
                } else if (parseInt(StoreSeconds) === 0) {
                    if (parseInt(StoreMinuets) > 0) {
                        dispatch(setMinutes(parseInt(StoreMinuets) - 1));
                        dispatch(setSeconds(59));
                    } else if (parseInt(StoreMinuets) === 0) {
                        if (parseInt(StoreHours) > 0) {
                            dispatch(setHours(parseInt(StoreHours) - 1));
                            dispatch(setMinutes(59));
                            dispatch(setSeconds(59));
                        } else {
                            window.location.replace('/logout')
                            clearInterval(countdown);
                        }
                    }
                }
            }, 1000);

            return () => clearInterval(countdown);
        }

    }, [StoreHours, StoreMinuets, StoreSeconds]);

    if (StoreAccessToken != "") {

        return (


            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand href="/"></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link href="/mix"></Nav.Link>
                            <Nav.Link href="/myjo"> ()</Nav.Link>
                            <Nav.Link href="/board/List"> </Nav.Link>
                            <Nav.Link href="/notify"></Nav.Link>

                        </Nav>
                        <Nav>
                            <Nav.Link >
                                {!(StoreHours === StoreMinuets === StoreSeconds === 0) && (
                                    <>
                                        {StoreHours < 10 ? `0${StoreHours}` : StoreHours} :
                                        {StoreMinuets < 10 ? `0${StoreMinuets}` : StoreMinuets} :
                                        {StoreSeconds < 10 ? `0${StoreSeconds}` : StoreSeconds}
                                    </>
                                )}

                            </Nav.Link>
                            <Nav.Link>{StoreNickname}</Nav.Link>
                            <Nav.Link href="/logout"></Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar >

        );
    }

    else {
        return (


            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand href="/"></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link href="/board/List"></Nav.Link>
                            <Nav.Link href="/notify"></Nav.Link>

                        </Nav>
                        <Nav>
                            <Nav.Link href="/login"></Nav.Link>
                            <Nav.Link href="/join"></Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

        );
    }

}




