import { ChangeCircle } from '@mui/icons-material';
import { Container, Box, TextField, Button } from '@mui/material';
import { right } from '@popperjs/core';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router';
import axiosInstance from '../setting/axiosInstance';

function BRead() {
    const StoreaccessToken = useSelector((state) => state.accessToken);
    const nickname = useSelector((state) => state.nickname);

    const { bno } = useParams();
    const [reply_board, setreply_board] = useState([]);
    const [article, setArticle] = useState("");
    const [modifyArticle, setModifyArticle] = useState(false);

    useEffect(() => {
        axiosInstance.defaults.headers.common['Authorization'] = `Bearer ${StoreaccessToken}`;

        axiosInstance.get('/board/read/' + bno)
            .then(res => {
                //          
                const modifiedReplies = res.data.slice(1).map(reply => ({ ...reply, isModifying: false }));
                setreply_board([res.data[0], ...modifiedReplies]);
                setArticle(res.data[0])
            })
            .catch(Error => {
                console.log(Error)
            })
    }, [])

    useEffect(() => {
        console.log(reply_board)
    }, [reply_board])

    //    
    const handlemodifyNicknameCheck = (bno, nickname) => {
        axiosInstance.get('/board/modify/' + bno + '/' + nickname)
            .then(res => {
                setModifyArticle(true);
            })
            .catch(error => {
                console.log(error)
            })
    }

    //   
    const handlemodifyBoard = (bno) => {
        const modifyTitle = document.getElementById(`modifyTitle`).value;
        const modifyContent = document.getElementById(`modifyContent`).value;
        axiosInstance.put('/board/modify',
            {
                bno: bno,
                content: modifyContent,
                title: modifyTitle,
                writer: nickname
            })
            .then(res => {
                setModifyArticle(false);
                window.location.reload();
            })
            .catch(Error => {
                console.log(Error)
            })
    }

    // 
    const handleBoardDeletecheck = (bno, writer) => {
        axiosInstance.get('/board/delete/' + bno + '/' + writer)
            .then(res => {
                if (window.confirm(" ?")) {
                    axiosInstance.delete('/board/delete', {
                        data: {
                            bno: bno
                        }
                    })
                        .then((res) => {
                            window.location.replace("/board/list")
                        })
                        .catch(error => {
                            console.log(error)
                        })
                }
            })
            .catch(Error => {
                console.log(Error)
            })
    }

    //   
    const handleReplyModifycheck = (rno, replyer) => {
        axiosInstance.get('/reply/modify/' + rno + '/' + replyer)
            .then(res => {
                setreply_board(prevReplies => {
                    return prevReplies.map(reply => {
                        if (reply.rno === rno) {
                            return { ...reply, isModifying: !reply.isModifying };
                        } else {
                            return reply;
                        }
                    });
                });
            })
            .catch(Error => {
                console.log(Error)
            })
    }

    //      
    const handleModify = (rno, count) => {
        const textFieldValue = document.getElementById(`reply${count + 1}`).value;
        axiosInstance.put('/reply/modify',
            {
                rno: rno,
                content: textFieldValue
            })
            .then(res => {
                window.location.reload();
            })
            .catch(Error => {
                console.log(Error)
            })
    }
    // 
    const handleReplyDeletecheck = (rno, replyer) => {
        axiosInstance.get('/reply/delete/' + rno + '/' + replyer)
            .then(res => {
                if (window.confirm(" ?")) {
                    axios.delete('/reply/delete', {
                        data: {
                            rno: rno
                        }
                    })
                        .then((res) => {
                            window.location.reload()
                        })
                        .catch(error => {
                            console.log(error)
                        })
                }
            })
            .catch(Error => {
                console.log(Error)
            })
    }

    // 
    const handleResigterReply = (bno, nickname) => {
        const textFieldValue = document.getElementById(`registerReply`).value;
        axiosInstance.post('/reply/register',
            {
                bno: bno,
                content: textFieldValue,
                replyer: nickname
            })
            .then(res => {
                window.location.reload();
            })
            .catch(Error => {
                console.log(Error)
            })
    }

    if (reply_board.length > 0) {

        const date = reply_board[0].regDate.substring(0, 20);
        if (reply_board.replyCount == 0) {
            return (
                <Container component="main" maxWidth="lg">
                    <h2 style={{ marginTop: "20px" }}></h2>
                    <Box sx={{ marginTop: 2 }}>
                        <TextField value={article.title} required fullWidth name='title' />
                        <TextField value={article.writer} required fullWidth name='title' />
                        <TextField value={article.content} style={{ marginTop: 10, height: "500px" }} InputProps={{ style: { height: 700 } }} multiline required fullWidth />
                    </Box>
                </Container>
            );
        }
        else {
            return (
                <Container component="main" maxWidth="lg">
                    <h2 style={{ marginTop: "20px" }}></h2>
                    <Box sx={{ marginTop: 2 }}>
                        {/*     ,   */}
                        {(nickname === article.writer) ? (
                            //    true   ,     
                            modifyArticle ? (
                                <>
                                    <TextField required fullWidth id='modifyTitle' />
                                    <Box alignContent='right'>
                                        <h4 style={{ textAlign: right }}>  : {article.writer}  </h4>
                                        <h4 style={{ textAlign: right }}>    : {date}</h4>
                                    </Box>
                                    <TextField style={{ marginTop: 10, height: "500px" }} InputProps={{ style: { height: 700 } }} multiline required fullWidth id='modifyContent' />
                                    <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2, float: right }} onClick={() => handlemodifyBoard(article.bno, nickname)}></Button>
                                </>
                            ) : (
                                <>
                                    <TextField value={article.title} required fullWidth name='title' />
                                    <Box alignContent='right'>
                                        <h4 style={{ textAlign: right }}>  : {article.writer}  </h4>
                                        <h4 style={{ textAlign: right }}>    : {date}</h4>
                                    </Box>
                                    <TextField value={article.content} style={{ marginTop: 10, height: "500px" }} InputProps={{ style: { height: 700 } }} multiline required fullWidth />
                                    <Box>
                                        <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2, float: right }} onClick={() => handleBoardDeletecheck(article.bno, nickname)}></Button>
                                        <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2, float: right }} onClick={() => handlemodifyNicknameCheck(article.bno, nickname)}></Button>
                                    </Box>
                                </>
                            )
                        ) : (
                            <><TextField value={article.title} required fullWidth />
                                <Box alignContent='right'>
                                    <h4 style={{ textAlign: right }}>  : {article.writer}  </h4>
                                    <h4 style={{ textAlign: right }}>    : {date}</h4>
                                </Box>
                                <TextField value={article.content} style={{ marginTop: 10, height: "500px" }} InputProps={{ style: { height: 700 } }} multiline required fullWidth /></>
                        )}

                    </Box>
                    <br />
                    <br />


                    <h3></h3>
                    <Box>
                        {reply_board.slice(1).map((row, count) => ( // :       
                            <div key={count}>
                                <div style={{ textAlign: 'right', display: 'flex', alignItems: 'center' }}>
                                    <span style={{ fontSize: 15, textAlign: 'left', marginRight: 'auto' }}>{row.replyer}</span>
                                    {row.replyer === nickname && ( // ,  
                                        <React.Fragment>
                                            <Button onClick={() => handleReplyModifycheck(row.rno, row.replyer)}></Button>
                                            <Button onClick={() => handleReplyDeletecheck(row.rno, row.replyer)}></Button>
                                        </React.Fragment>
                                    )}
                                </div>

                                <TextField value={row.content} required fullWidth name={`reply${count + 1}`} />
                                {row.isModifying && (
                                    <div>
                                        <div>  </div>
                                        <TextField id={`reply${count + 1}`} required sx={{ width: 800 }} name={`reply${count + 1}`} />
                                        <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2 }} onClick={() => handleModify(row.rno, count)}> </Button>
                                    </div>
                                )}
                            </div>
                        ))}
                    </Box>
                    {
                        nickname != "" && (
                            <div>
                                <br />
                                <h2> </h2>
                                <TextField id='registerReply' required sx={{ width: 800 }} />
                                <Button type='submit' variant='contained' sx={{ margin: 2, marginTop: 2 }} onClick={() => handleResigterReply(article.bno, nickname)}> </Button>
                            </div>
                        )
                    }

                </Container >
            );
        }
    }
}

export default BRead;