import React, { useEffect, useState } from 'react';
import { Box, Container, Button, styled, Pagination } from '@mui/material';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import axios from 'axios';
import { Link } from 'react-router-dom';

function List() {
    const StyledTableCell = styled(TableCell)(({ theme }) => ({
        [`&.${tableCellClasses.head}`]: {
            backgroundColor: theme.palette.common.black,
            color: theme.palette.common.white,
        },
        [`&.${tableCellClasses.body}`]: {
            fontSize: 14,
        },
    }));

    const StyledTableRow = styled(TableRow)(({ theme }) => ({
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        }
    }));
    const [boardlist, setboardlist] = useState([{
        dtoList: [],
        totalPage: "",
        page: "",
        size: "",
        start: "",
        end: "",
        prev: "",
        next: "",
        pageList: []
    }])
    const [board, setboard] = useState([])

    //   
    useEffect(() => {
        axios.get('/board/list')
            .then(response => {
                setboardlist(response.data)
            })
            .catch(Error =>
                console.log(Error)
            )
    }, [])

    //    
    useEffect(() => {
        console.log(boardlist)
        setboard(boardlist.dtoList)
    }, [boardlist])

    //    
    useEffect(() => {
        console.log(board)
    }, [board])


    const handlechange = (page) => {
        axios.get('/board/list', {
            params: {
                page: page
            }
        })
            .then(response => {
                setboardlist(response.data)
            })
            .catch(Error =>
                console.log(Error)
            )
    }


    const writeClick = () => {
        window.location.replace('/board/write')
    }

    if (Array.isArray(board)) {
        return (
            <Container component="main" maxWidth="lg">
                <h2 style={{ marginTop: "50px" }}> </h2>
                <Box sx={{ marginTop: 8, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                    <TableContainer component={Paper}>
                        <Table sx={{ minWidth: 700 }} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell align="center"></StyledTableCell>
                                    <StyledTableCell sx={{ width: 150 }} align="right"></StyledTableCell>
                                    <StyledTableCell sx={{ width: 150 }} align="right"></StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {board.map((row, rowcount) => {
                                    //2023-07-14T10:45:00.246510   {}   return     .
                                    const date = row.regDate.substring(0, 10);
                                    return (
                                        <StyledTableRow key={row.bno}>
                                            <StyledTableCell align="right" >
                                                <Link to={`/board/read/${row.bno}`}>
                                                    {row.title}
                                                </Link>
                                            </StyledTableCell>
                                            <StyledTableCell align="right">{row.writer}</StyledTableCell>
                                            <StyledTableCell size='small' align="right">{date}</StyledTableCell>
                                        </StyledTableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Box>
                <Box justifyContent={"center"} alignContent={"center"} display={"flex"} sx={{ margin: 2 }}>
                    <Pagination
                        count={boardlist.totalPage}
                        onChange={handlechange}
                    />
                </Box>

                <Button type='submit' variant='contained' sx={{ marginTop: 2, marginLeft: 120 }} onClick={writeClick}></Button>
            </Container>
        );
    }
    else {
        return (
            <div>
                 
            </div>
        )

    }
}

export default List;