import { Container, Box, TextField, Button } from '@mui/material';
import axios from 'axios';
import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import axiosInstance from '../setting/axiosInstance';

function Write() {
    const StoreaccessToken = useSelector((state) => state.accessToken);

    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const nickname = useSelector((state) => state.nickname);

    const handleTitle = (e) => {
        setTitle(e.target.value);
    }
    const handleContent = (e) => {
        setContent(e.target.value);
    }

    const writeClick = () => {
        axiosInstance.defaults.headers.common['Authorization'] = `Bearer ${StoreaccessToken}`;

        console.log(content)
        axiosInstance.post('/board/write', {
            title: title,
            content: content,
            writer: nickname
        })
            .then((res) => {
                alert('');
                window.location.replace('/');
            })
            .catch(error => {
                console.log(error)
            })

    }

    return (
        <Container component="main" maxWidth="lg">
            <h2 style={{ marginTop: "20px" }}> </h2>
            <Box sx={{ marginTop: 2 }}>
                <TextField label="" required fullWidth onChange={handleTitle} />
                <TextField label="" sx={{ marginTop: 3 }} InputProps={{ style: { height: 500, maxHeight: 500 } }} maxRows={20} multiline fullWidth onChange={handleContent} />
            </Box>
            <Button type='submit' variant='contained' sx={{ marginTop: 2, marginLeft: 130 }} onClick={writeClick}></Button>
        </Container>


    );
}


export default Write;