import axios from "axios";
import React, { useEffect, useState } from "react";
import axiosInstance from "./setting/axiosInstance";


function Main() {
    const [visit, setvisit] = useState("");

    useEffect(() => {
        axiosInstance.get('/visit/count')
            .then(res => {
                setvisit(res.data)
                console.log(visit)
            })
            .catch(error => {
                console.log(error)
            })
    }, [])

    return (
        <>
            {
                visit.total > 0 && (
                    <table align="center" style={{ width: "200px", backgroundColor: "#9999ff", color: "black", textAlign: "center", marginLeft: "500px" }}>
                        <tbody>
                            <th colSpan="2"> today </th>
                            <tr>
                                <td>{visit.today} :  </td>
                            </tr>
                            <th colSpan="2"> total </th>
                            <tr>
                                <td>{visit.total} :  </td>
                            </tr>
                        </tbody>
                    </table>
                )
            }
        </>

    );
}

export default Main;